"use server"

import { stripe } from "@/lib/stripe"
import { PRODUCTS } from "@/lib/products"

export async function startCheckoutSession(productId: string) {
  if (!stripe) {
    throw new Error("Stripe Êú™ÈÖçÁΩÆÔºåËØ∑‰ΩøÁî®ÂÖ∂‰ªñÊîØ‰ªòÊñπÂºè")
  }

  const product = PRODUCTS.find((p) => p.id === productId)

  if (!product) {
    throw new Error(`‰∫ßÂìÅ "${productId}" ‰∏çÂ≠òÂú®`)
  }

  if (product.priceInCents === 0) {
    throw new Error("ÂÖçË¥π‰∫ßÂìÅÊó†ÈúÄÊîØ‰ªò")
  }

  try {
    // ÂàõÂª∫StripeÁªìË¥¶‰ºöËØùÔºåÊîØÊåÅÂ§öÁßçÊîØ‰ªòÊñπÂºè
    const session = await stripe.checkout.sessions.create({
      ui_mode: "embedded",
      redirect_on_completion: "never",
      line_items: [
        {
          price_data: {
            currency: "cny", // ‰ΩøÁî®‰∫∫Ê∞ëÂ∏Å
            product_data: {
              name: product.name,
              description: product.description,
            },
            unit_amount: product.priceInCents,
          },
          quantity: 1,
        },
      ],
      mode: "payment",
      payment_method_types: ["card"],
    })

    return session.client_secret
  } catch (error: any) {
    console.error("[v0] Stripe session creation error:", error)
    throw new Error(error?.message || "ÂàõÂª∫ÊîØ‰ªò‰ºöËØùÂ§±Ë¥•")
  }
}

export async function getPaymentStatus(sessionId: string) {
  if (!stripe) {
    throw new Error("Stripe Êú™ÈÖçÁΩÆ")
  }

  const session = await stripe.checkout.sessions.retrieve(sessionId)
  return {
    status: session.status,
    payment_status: session.payment_status,
    customer_email: session.customer_details?.email,
  }
}
